@font-face {
  font-family: 'Big City Grotesque Pro';
  src: url(../fonts/BigCityGrotesquePro-Bold.woff) format('woff');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'Big City Grotesque Pro';
  src: url(../fonts/BigCityGrotesquePro-Black.woff) format('woff');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'Big City Grotesque Pro';
  src: url(../fonts/BigCityGrotesquePro-Regular.woff) format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'PTRoubleSans';
  src: url(../fonts/PTRoubleSans.woff) format('woff');
  font-weight: 400;
  font-style: normal;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
  margin: 0;
  font-family: 'Big City Grotesque Pro', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.25;
  color: #788d98;
  font-weight: 300;
  min-width: 320px;
  left: 0;
  right: 0;
  background-color: #ccd9e3;
}
.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 16px;
  padding-right: 16px;
  width: auto;
}
@media (min-width: 768px) {
  .container {
    width: 722px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 920px;
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1230px;
  }
}
.container--relative {
  position: relative;
}
a {
  text-decoration: none;
  color: #788d98;
  outline: none;
}
img {
  max-width: 100%;
  height: auto;
}
main {
  flex-grow: 1;
}
textarea,
input[type="text"],
input[type="tel"],
input[type="email"],
input[type="button"],
input[type="submit"] {
  -webkit-appearance: none;
}
.b-rub {
  font-family: 'PTRoubleSans', Arial, sans-serif;
  font-size: 24px;
  display: inline-block;
  vertical-align: top;
}
form select.error,
form textarea.error,
form input.error {
  border: 3px solid #e01d42;
}
form select.error::placeholder,
form textarea.error::placeholder,
form input.error::placeholder {
  color: #788d98;
  font-size: 20px;
  font-weight: 700;
}
@media (min-width: 1280px) {
  form select.error::placeholder,
  form textarea.error::placeholder,
  form input.error::placeholder {
    font-size: 24px;
  }
}
.red {
  color: #e01d42;
}
.i {
  font-style: italic;
}
